@font-face {
  font-family: "number";
  src: url(../../fonts/JerseyM54-aLX9.ttf);
}
.container {
  min-height: 100vh;
  margin: 0 auto;
  font-family: "number", Cambria, Cochin, Georgia;
  width: 95%;
}

.boxlist {
  display: flex;
  margin-top: 15px;
  justify-content: space-between;
  align-items: center;
  border-radius: 20px;
  height: 20px;
  cursor: pointer;
  background-image: url(../../assets/default/dine.svg);
  background: linear-gradient(
    100deg,
    rgba(47, 140, 233, 1) 50%,
    rgba(16, 119, 221, 1) 50%
  );

  background-position: center;
  background-size: contain;
  position: relative;
  color: white;
  box-shadow: rgba(0, 0, 0, 0.15) 1.95px 1.95px 2.6px;
  padding: 20px;
  transition: 0.3s;
  transform: translateX(-20px);
  animation: anime-left 0.3s forwards;
}

@keyframes anime-left {
  to {
    transform: translateX(0);
  }
}
.boxlist:hover {
  color: #3a3a3a;
  font-size: 1.2rem;
}

.boxlist.block {
  opacity: 40%;
  cursor: auto;
}

.boxlist.block:hover {
  opacity: 40%;
  color: white;
  font-size: 1rem;
}
.boxlistMaster {
  margin-top: 30px;
}

.title {
  font-family: var(--type-first);
  margin: 1rem 0;
  color: #0e001e;

  position: relative;
  font-weight: 500;
  font-size: 1.7rem;
  line-height: 1;
}

.title::after {
  height: 15px;
  content: "";
  display: block;
  width: 15px;
  border-radius: 2px;
  position: absolute;
  bottom: 0;
  z-index: -1;
  background-color: #1077dd;
}

.score {
  display: flex;
  align-items: center;
}
