.container {
  min-height: 100vh;
  margin: 0 auto;
  font-family: "number", Cambria, Cochin, Georgia;
  width: 95%;
}
.title {
  font-family: var(--type-first);
  margin: 1rem 0;
  color: #333;

  position: relative;
  font-weight: 500;
  font-size: 1.7rem;
  line-height: 1;
}

.title::after {
  height: 15px;
  content: "";
  display: block;
  width: 15px;
  border-radius: 2px;
  position: absolute;
  bottom: 0;
  z-index: -1;
  background-color: #7ddd10;
}

.message {
  font-size: 0.8rem;
  color: rgb(124, 124, 124);
  margin-bottom: 5px;
}

.containerNav {
  display: flex;
  align-items: center;
  justify-content: space-between;
}

.button {
  background-color: #7ddd10e5;
  height: 40px;
  box-shadow: rgba(0, 0, 0, 0.15) 1.95px 1.95px 2.6px;
  display: flex;
  transition: 0.3s;
  color: rgb(66, 66, 66);
  align-items: center;
  justify-content: center;
  background: linear-gradient(to left top, #7ddd10 50%, #7acf18 50%);
  width: 90px;
  border-radius: 5px;
}

.button:hover {
  font-size: 1.1rem;
  color: rgb(241, 232, 222);
}

.containerNav {
  border-bottom: 1px solid rgb(221, 215, 215);
}

.online {
  padding: 10px;
}
.containerUser {
  width: 90%;
  margin: 0 auto;
}
.user {
  display: flex;
  align-items: center;
  margin-top: 10px;
  border-radius: 5px;
  padding: 5px;
  box-shadow: rgba(0, 0, 0, 0.15) 1.95px 1.95px 2.6px;
  background-color: #ffffff;
  justify-content: space-between;
}

.imageUser {
  height: 50px;
  width: 50px;
  background-image: url(../../assets/profile/profile.png);
  background-size: contain;
  border-radius: 5px;
}

.score {
  display: flex;
  border-radius: 10px;
  padding: 8px;
  gap: 10px;
  align-items: center;
}
